@import "../../index.scss";
.container {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  padding: 80px 15px 60px;
  width: 100%;
  @include tablet {
    margin-left: 0;
    padding: 80px 15px 165px;
    min-width: 320px;
  }
  @include desktop {
    padding: 80px 15px 120px;
  }
}
